import { Component, TagComponent, Types } from 'ecsy';

export class DroidsGameComponent extends Component {
	resetGame() {
		this.gameActive = false;
		this.gameTimer = 0;
		this.droidSpawnTimer = 5;
		this.droidSpawnMultiplier = 1;
		this.playerHitPoints = 10;
	}
}

DroidsGameComponent.schema = {
	crateBase: { type: Types.Ref, default: undefined },
	crateLid: { type: Types.Ref, default: undefined },
	droidPrototype: { type: Types.Ref, default: undefined },

	gameActive: { type: Types.Boolean, default: false },
	gameTimer: { type: Types.Number, default: 0 },
	droidSpawnTimer: { type: Types.Number, default: 5 },
	droidSpawnMultiplier: { type: Types.Number, default: 1 },

	playerHitPoints: { type: Types.Number, default: 10 },
	playerHeadCollider: { type: Types.Ref, default: null },
};

export class PistolComponent extends Component {}

PistolComponent.schema = {
	muzzle: { type: Types.Ref, default: undefined },
};

export class PistolLaser extends TagComponent {}

export const DROID_STATE = {
	MOVING: 0,
	IN_PLACE: 1,
};

export class DroidComponent extends Component {}

DroidComponent.schema = {
	hitPoints: { type: Types.Number, default: 2 },
	state: { type: Types.Number, default: DROID_STATE.MOVING },
	targetPosition: { type: Types.Ref, default: undefined },
	speed: { type: Types.Number, default: 5 },
	attackTimer: { type: Types.Number, default: 3 },
};

export class DroidLaser extends TagComponent {}
