import * as THREE from 'three';

import {
	GRAB_STATE,
	GrabbableComponent,
} from '../../components/GrabbableComponent';
import {
	PistolComponent,
	PistolLaser,
} from '../../components/DroidsGameComponents';

import { ExpandingParticleCloud } from '../../lib/ExpandingParticleCloud';
import { InteractionSystem } from '../../utils/ECSYUtils';
import { TRIGGERS } from '../../lib/ControllerInterface';
import { spawnLaserProjectile } from './DamageSystem';

export class PistolSystem extends InteractionSystem {
	init() {
		this._helperVec3 = new THREE.Vector3();
		this._helperQuat = new THREE.Quaternion();
	}

	onExecute() {
		this.queries.pistol.results.forEach((entity) => {
			if (
				entity.getComponent(GrabbableComponent).state === GRAB_STATE.IN_HAND
			) {
				const pistolComponent = entity.getComponent(PistolComponent);
				const controller = this.controllerInterfaces[
					entity.getComponent(GrabbableComponent).handKey
				];
				if (controller.triggerJustPressed(TRIGGERS.INDEX_TRIGGER)) {
					this.shoot(pistolComponent.muzzle);
				}
			}
		});
	}

	shoot(muzzle) {
		const position = muzzle.getWorldPosition(this._helperVec3);
		const direction = new THREE.Vector3(0, 0, 1).applyQuaternion(
			muzzle.getWorldQuaternion(this._helperQuat),
		);

		const laserEntity = spawnLaserProjectile(
			this.gameStateComponent.scene,
			this.world,
			position,
			direction,
			{ color: 0x8d1b1f, speed: 20, timeToLive: 1 },
		);
		laserEntity.addComponent(PistolLaser);

		const particleCloud = new ExpandingParticleCloud(
			10,
			direction.clone().multiplyScalar(3),
			0.2,
		);
		this.gameStateComponent.scene.add(particleCloud);
		particleCloud.position.copy(position);
	}
}

PistolSystem.addQueries({
	pistol: { components: [PistolComponent, GrabbableComponent] },
});
