import * as THREE from 'three';

import {
	DroidComponent,
	DroidLaser,
	DroidsGameComponent,
	PistolLaser,
} from '../../components/DroidsGameComponents';
import { GameSystem, deleteEntity, getOnlyEntity } from '../../utils/ECSYUtils';

import { ExpandingParticleCloud } from '../../lib/ExpandingParticleCloud';
import { ExpandingRing } from '../../lib/ExpandingRing';
import { Object3DComponent } from '../../components/Object3DComponent';
import { RigidBodyComponent } from '../../components/RigidBodyComponent';

export class DamageSystem extends GameSystem {
	init() {
		this.raycaster = new THREE.Raycaster();
		this.game = null;
		this.entitiesToDelete = new Set();
	}

	onExecute(delta, _time) {
		this.game = getOnlyEntity(this.queries.game, false)?.getMutableComponent(
			DroidsGameComponent,
		);
		if (!this.game) return;

		this.entitiesToDelete.clear();

		const droidObjects = [];
		this.queries.droid.results.forEach((entity) => {
			const droidObject = entity.getComponent(Object3DComponent).value;
			droidObject.entity = entity;
			droidObjects.push(droidObject);
		});

		const laserEntities = [].concat(
			this.queries.pistolLaser.results,
			this.queries.droidLaser.results,
		);
		laserEntities.forEach((laserEntity) => {
			const laserObject = laserEntity.getComponent(Object3DComponent).value;
			const laserRigidBody = laserEntity.getMutableComponent(
				RigidBodyComponent,
			);
			this.raycaster.set(laserObject.position, laserRigidBody.direction);
			this.raycaster.far = laserRigidBody.speed * delta;

			const intersect = this.raycaster.intersectObjects(
				laserEntity.hasComponent(PistolLaser)
					? droidObjects
					: [this.game.playerHeadCollider],
			)[0];
			if (intersect) {
				if (laserEntity.hasComponent(PistolLaser)) {
					this.onDroidHit(intersect, laserRigidBody);
				} else {
					this.onPlayerHit(intersect);
				}
				this.entitiesToDelete.add(laserEntity);
			}
		});

		this.entitiesToDelete.forEach(deleteEntity);
	}

	onDroidHit(intersect, laserRigidBody) {
		const droidEntity = intersect.object.entity;
		droidEntity.getMutableComponent(DroidComponent).hitPoints -= 1;
		let particleCloud = new ExpandingParticleCloud(
			5,
			new THREE.Vector3().copy(laserRigidBody.direction).negate(),
			0.5,
		);
		this.gameStateComponent.scene.add(particleCloud);
		particleCloud.position.copy(intersect.point);
	}

	onPlayerHit(intersect) {
		this.game.playerHitPoints -= 1;
		const expandingRing = new ExpandingRing({
			innerRadius: 0.01,
			outerRadius: 0.02,
			timeToLive: 0.3,
			color: 0x00e000,
		});
		this.gameStateComponent.scene.add(expandingRing);
		expandingRing.position.copy(intersect.point);
	}
}

DamageSystem.addQueries({
	game: { components: [DroidsGameComponent] },
	droid: { components: [DroidComponent, Object3DComponent] },
	pistolLaser: {
		components: [RigidBodyComponent, Object3DComponent, PistolLaser],
	},
	droidLaser: {
		components: [RigidBodyComponent, Object3DComponent, DroidLaser],
	},
});

export const spawnLaserProjectile = (
	scene,
	world,
	position,
	direction,
	props = {},
) => {
	let laserEntity = world.createEntity();
	let laserMesh = new THREE.Mesh(
		new THREE.BoxGeometry(0.04, 0.04, 0.3),
		new THREE.MeshBasicMaterial({ color: props.color ?? 0x8d1b1f }),
	);
	scene.add(laserMesh);
	laserMesh.position.copy(position);
	laserMesh.lookAt(new THREE.Vector3().addVectors(position, direction));
	laserEntity.addComponent(Object3DComponent, { value: laserMesh });
	laserEntity.addComponent(RigidBodyComponent, {
		direction,
		speed: props.speed ?? 20,
		dragDecel: 0,
		perishable: true,
		timeToLive: props.timeToLive ?? 1,
	});
	return laserEntity;
};
