import * as THREE from 'three';

import {
	DROID_STATE,
	DroidComponent,
	DroidLaser,
} from '../../components/DroidsGameComponents';
import { GameSystem, deleteEntity } from '../../utils/ECSYUtils';

import { ExpandingParticleCloud } from '../../lib/ExpandingParticleCloud';
import { Object3DComponent } from '../../components/Object3DComponent';
import { spawnLaserProjectile } from './DamageSystem';

export class DroidSystem extends GameSystem {
	init() {
		this.raycaster = new THREE.Raycaster();
		this.entitiesToDelete = new Set();

		this._helperVec3 = new THREE.Vector3();
		this._helperQuat = new THREE.Quaternion();
	}

	onExecute(delta, _time) {
		this.entitiesToDelete.clear();

		this.updateDroids(delta);

		this.entitiesToDelete.forEach(deleteEntity);
	}

	updateDroids(delta) {
		this.queries.droid.results.forEach((entity) => {
			let droidComponent = entity.getMutableComponent(DroidComponent);
			let droidObject = entity.getComponent(Object3DComponent).value;
			if (droidComponent.hitPoints <= 0) {
				this.entitiesToDelete.add(entity);
				let particleCloud = new ExpandingParticleCloud(20, new THREE.Vector3());
				this.gameStateComponent.scene.add(particleCloud);
				particleCloud.position.copy(droidObject.position);
			} else {
				if (droidComponent.state === DROID_STATE.MOVING) {
					this._helperVec3.subVectors(
						droidComponent.targetPosition,
						droidObject.position,
					);
					if (this._helperVec3.length() <= droidComponent.speed * delta) {
						droidObject.position.copy(droidComponent.targetPosition);
						droidComponent.state = DROID_STATE.IN_PLACE;
					} else {
						droidObject.position.add(
							this._helperVec3
								.normalize()
								.multiplyScalar(droidComponent.speed * delta),
						);
					}
				} else if (droidComponent.state === DROID_STATE.IN_PLACE) {
					droidComponent.attackTimer -= delta;
					if (droidComponent.attackTimer <= 0) {
						this.shoot(droidObject);
						droidComponent.attackTimer = 3;
					}
				}
			}
		});
	}

	shoot(droidObject) {
		const position = droidObject.getWorldPosition(this._helperVec3);
		const direction = this.gameStateComponent.renderer.xr
			.getCamera()
			.getWorldPosition(new THREE.Vector3())
			.sub(position)
			.normalize();

		const laserEntity = spawnLaserProjectile(
			this.gameStateComponent.scene,
			this.world,
			position,
			direction,
			{ color: 0x00e000, speed: 10, timeToLive: 1 },
		);
		laserEntity.addComponent(DroidLaser);
	}
}

DroidSystem.addQueries({
	droid: { components: [DroidComponent, Object3DComponent] },
});
