import { ArcadeSwitch, SwitchComponent } from './components/SwitchComponent';
import {
	AstroidBlasterGameComponent,
	AstroidComponent,
	TurretLaser,
} from './components/AstroidBlasterGameComponent';
import {
	Collider,
	ObjectCollider,
	VerticalCollider,
} from './components/TagComponents';
import {
	DroidComponent,
	DroidLaser,
	DroidsGameComponent,
	PistolComponent,
	PistolLaser,
} from './components/DroidsGameComponents';
import {
	LeftController,
	Occupied,
	RightController,
	VrControllerComponent,
} from './components/VrControllerComponent';

import { AstroidBlasterGameSystem } from './systems/AstroidBlasterGameSystem';
import { AstroidSystem } from './systems/AstroidSystem';
import { DroidSystem } from './systems/droidsGame/DroidSystem';
import { DamageSystem as DroidsGameDamageSystem } from './systems/droidsGame/DamageSystem';
import { DroidsGameSystem } from './systems/droidsGame/DroidsGameSystem';
import { GameStateComponent } from './components/GameStateComponent';
import { GrabbableComponent } from './components/GrabbableComponent';
import { HandAnimationSystem } from './systems/HandAnimationSystem';
import { HandComponent } from './components/HandComponent';
import { Object3DComponent } from './components/Object3DComponent';
import { ObjectGNTSystem } from './systems/ObjectGNTSystem';
import { PistolSystem } from './systems/droidsGame/PistolSystem';
import { RenderingSystem } from './systems/RenderingSystem';
import { RigidBodyComponent } from './components/RigidBodyComponent';
import { RigidBodyPhysicsSystem } from './systems/RigidBodyPhysicsSystem';
import { SceneCreationSystem } from './systems/SceneCreationSystem';
import { SnapTurnSystem } from './systems/SnapTurnSystem';
import { SwitchSystem } from './systems/SwitchSystem';
import { TeleportSystem } from './systems/TeleportSystem';
import { TurretSystem } from './systems/TurretSystem';
import { VrInputSystem } from './systems/VrInputSystem';
import { World } from 'ecsy';
import { WristMenuSystem } from './systems/WristMenuSystem';

export const setupECSY = () => {
	let world = new World();

	registerTagComponents(world);

	registerComponents(world);

	registerSystems(world);

	return world;
};

/**
 * Register TagComponents
 * @param {World} world
 */
const registerTagComponents = (world) => {
	world.registerComponent(Collider);
	world.registerComponent(VerticalCollider);
	world.registerComponent(ObjectCollider);
	world.registerComponent(RightController);
	world.registerComponent(LeftController);
	world.registerComponent(Occupied);
	world.registerComponent(ArcadeSwitch);
	world.registerComponent(TurretLaser);
	world.registerComponent(PistolLaser);
	world.registerComponent(DroidLaser);
};

/**
 * Regitser normal components
 * @param {World} world
 */
const registerComponents = (world) => {
	world.registerComponent(GameStateComponent);
	world.registerComponent(Object3DComponent);
	world.registerComponent(VrControllerComponent);
	world.registerComponent(RigidBodyComponent);
	world.registerComponent(HandComponent);
	world.registerComponent(GrabbableComponent);
	world.registerComponent(SwitchComponent);
	world.registerComponent(AstroidBlasterGameComponent);
	world.registerComponent(AstroidComponent);
	world.registerComponent(DroidsGameComponent);
	world.registerComponent(PistolComponent);
	world.registerComponent(DroidComponent);
};

/**
 * Register systems in execution order
 * @param {World} world
 */
const registerSystems = (world) => {
	world.registerSystem(SceneCreationSystem);
	// input
	world.registerSystem(VrInputSystem);
	world.registerSystem(HandAnimationSystem);
	// locomotion
	world.registerSystem(TeleportSystem);
	world.registerSystem(SnapTurnSystem);
	// object manipulation
	world.registerSystem(ObjectGNTSystem);
	world.registerSystem(RigidBodyPhysicsSystem);
	world.registerSystem(SwitchSystem);
	// Astroids mini game
	world.registerSystem(AstroidBlasterGameSystem);
	world.registerSystem(TurretSystem);
	world.registerSystem(AstroidSystem);
	// Droids mini game
	world.registerSystem(DroidsGameSystem);
	world.registerSystem(PistolSystem);
	world.registerSystem(DroidSystem);
	world.registerSystem(DroidsGameDamageSystem);
	world.getSystem(DroidSystem).stop();
	world.getSystem(DroidsGameDamageSystem).stop();

	world.registerSystem(WristMenuSystem);
	world.registerSystem(RenderingSystem);

	// world.getSystem(AstroidBlasterGameSystem).stop();
	world.getSystem(DroidsGameSystem).stop();
};
