import * as THREE from 'three';

import {
	ArcadeSwitch,
	SwitchComponent,
} from '../../components/SwitchComponent';
import {
	DroidComponent,
	DroidsGameComponent,
	PistolComponent,
} from '../../components/DroidsGameComponents';
import { GameSystem, deleteEntity, getOnlyEntity } from '../../utils/ECSYUtils';
import {
	RigidBodyComponent,
	createDefaultRigidBodySchema,
} from '../../components/RigidBodyComponent';

import { DROIDS_CONSTANTS } from '../../Constants';
import { DamageSystem } from './DamageSystem';
import { DroidSystem } from './DroidSystem';
import { GLTFLoader } from 'three/examples/jsm/loaders/GLTFLoader';
import { GrabbableComponent } from '../../components/GrabbableComponent';
import { Object3DComponent } from '../../components/Object3DComponent';

export class DroidsGameSystem extends GameSystem {
	init() {
		this.assetLoaded = false;
		this.droidSpawnBox = null;
		this.droidAttackBox = null;
		this.droidPrototype = null;
		this._helperVec3 = new THREE.Vector3();
		this._helperQuat = new THREE.Quaternion();
		this.raycaster = new THREE.Raycaster();
		this.droidEntities = new Set();
	}

	onExecute(delta, _time) {
		if (!this.assetLoaded) {
			this.loadGameAssets(this.gameStateComponent.scene);
			this.assetLoaded = true;
		}

		let game = getOnlyEntity(
			this.queries.droidsGame,
			false,
		)?.getMutableComponent(DroidsGameComponent);

		if (!game) return;

		this.queries.arcadeSwitch.results.forEach((entity) => {
			let switchComponent = entity.getComponent(SwitchComponent);
			if (switchComponent.valueJustChanged) {
				if (game.gameActive && game.gameTimer > 5) {
					this.onGameEnd(game);
				} else if (!game.gameActive) {
					this.onGameStart(game);
				}
			}
		});

		if (game.gameActive) {
			this.executeGame(delta, game);
		}
	}

	loadGameAssets(scene) {
		let loader = new GLTFLoader();
		let crateBase, crateLid, droidPrototype, pistolPrototype;
		let pistolMounts = [];

		loader.load(DROIDS_CONSTANTS.GLTF_PATH, (gltf) => {
			let object = gltf.scene;
			object.children.forEach((node) => {
				switch (node.name) {
					case DROIDS_CONSTANTS.CRATE_BASE_NAME:
						crateBase = node;
						break;
					case DROIDS_CONSTANTS.CRATE_LID_NAME:
						crateLid = node;
						break;
					case DROIDS_CONSTANTS.DROID_NAME:
						droidPrototype = node;
						break;
					case DROIDS_CONSTANTS.DROID_SPAWN_ZONE:
						this.droidSpawnBox = new THREE.Box3();
						this.droidSpawnBox.setFromObject(node);
						break;
					case DROIDS_CONSTANTS.DROID_ATTACK_ZONE:
						this.droidAttackBox = new THREE.Box3();
						this.droidAttackBox.setFromObject(node);
						break;
					case DROIDS_CONSTANTS.PISTOL_NAME:
						pistolPrototype = node;
						break;
					default:
						if (node.name.startsWith(DROIDS_CONSTANTS.PISTOL_MOUNT_PREFIX)) {
							pistolMounts.push(node);
						}
						break;
				}
			});
			scene.attach(crateBase);
			// crateBase.attach(crateLid);
			pistolMounts.forEach((pistolMount) => {
				crateBase.attach(pistolMount);
				const pistolObject = pistolPrototype.clone();
				pistolObject.position.copy(
					pistolMount.getWorldPosition(this._helperVec3),
				);
				pistolObject.quaternion.copy(
					pistolMount.getWorldQuaternion(this._helperQuat),
				);
				const entity = this.world.createEntity();
				entity.addComponent(GrabbableComponent, {
					grabSpaceTransformOverride:
						DROIDS_CONSTANTS.PISTOL_GRAB_SPACE_TRANSFORM,
				});
				entity.addComponent(RigidBodyComponent, createDefaultRigidBodySchema());
				entity.addComponent(Object3DComponent, { value: pistolObject });
				entity.addComponent(PistolComponent, {
					muzzle: pistolObject.getObjectByName(
						DROIDS_CONSTANTS.PISTOL_MUZZLE_NAME,
					),
				});
				scene.attach(pistolObject);
			});
			this.droidPrototype = droidPrototype;

			const playerHeadCollider = new THREE.Mesh(
				new THREE.SphereGeometry(0.2, 8, 8),
				new THREE.MeshBasicMaterial(),
			);
			playerHeadCollider.visible = false;
			scene.add(playerHeadCollider);

			const entity = this.world.createEntity();
			entity.addComponent(DroidsGameComponent, {
				crateBase,
				crateLid,
				droidPrototype,
				playerHeadCollider,
			});
		});
	}

	spawnDroid() {
		const droidObject = this.droidPrototype.clone();
		droidObject.position.copy(randomPointInBox3(this.droidSpawnBox));
		this.gameStateComponent.scene.add(droidObject);

		const droidEntity = this.world.createEntity();
		droidEntity.addComponent(Object3DComponent, { value: droidObject });
		droidEntity.addComponent(DroidComponent, {
			targetPosition: randomPointInBox3(this.droidAttackBox),
		});
		this.droidEntities.add(droidEntity);
	}

	onGameEnd(game) {
		console.log('game over');
		game.resetGame();
		this.world.getSystem(DamageSystem).stop();
		this.world.getSystem(DroidSystem).stop();
	}

	onGameStart(game) {
		this.droidEntities.forEach((entity) => {
			if (entity) {
				deleteEntity(entity);
			}
		});
		this.droidEntities.clear();
		game.gameActive = true;
		this.world.getSystem(DamageSystem).play();
		this.world.getSystem(DroidSystem).play();
	}

	executeGame(delta, game) {
		game.playerHeadCollider.position.copy(
			this.gameStateComponent.renderer.xr
				.getCamera()
				.getWorldPosition(this._helperVec3),
		);
		game.gameTimer += delta;
		game.droidSpawnTimer -= delta;
		game.droidSpawnMultiplier = 1 + (game.gameTimer / 2) * 0.1;
		if (game.droidSpawnTimer <= 0) {
			this.spawnDroid();
			game.droidSpawnTimer = 5 / game.droidSpawnMultiplier;
		}
		if (game.playerHitPoints <= 0) {
			this.onGameEnd(game);
		}
	}
}

DroidsGameSystem.addQueries({
	arcadeSwitch: { components: [SwitchComponent, ArcadeSwitch] },
	droidsGame: { components: [DroidsGameComponent] },
});

const randomPointInBox3 = (box3) => {
	const x = Math.random() * (box3.max.x - box3.min.x) + box3.min.x;
	const y = Math.random() * (box3.max.y - box3.min.y) + box3.min.y;
	const z = Math.random() * (box3.max.z - box3.min.z) + box3.min.z;
	return new THREE.Vector3(x, y, z);
};
